import React, { useState, useEffect } from 'react';
import { Plano } from '@/services/api/plano.service';
import Input from '@/components/form/input/InputField';
import Button from '@/components/ui/button/Button';
import Label from '@/components/form/Label';
import Select from '@/components/ui/Select';

type PlanoFormProps = {
  plano?: Plano | null;
  onSubmit: (data: any) => void;
  onCancel: () => void;
};

const PlanoForm: React.FC<PlanoFormProps> = ({
  plano,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState({
    nome: '',
    valor: '',
    dias: '',
    status: 'ATIVO' as 'ATIVO' | 'INATIVO'
  });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (plano) {
      setFormData({
        nome: plano.nome || '',
        valor: plano.valor.toString(),
        dias: plano.dias.toString(),
        status: plano.status || 'ATIVO'
      });
    }
  }, [plano]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      const data = {
        nome: formData.nome,
        valor: parseFloat(formData.valor),
        dias: parseInt(formData.dias),
        status: formData.status
      };
      onSubmit(data);
    } catch (error) {
      console.error('Erro ao processar formulário:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <Label htmlFor="nome">Nome do Plano *</Label>
          <Input
            id="nome"
            value={formData.nome}
            onChange={(e) => handleChange('nome', e.target.value)}
            required
            placeholder="Digite o nome do plano"
          />
        </div>
        <div>
          <Label htmlFor="valor">Valor (R$) *</Label>
          <Input
            id="valor"
            type="number"
            step={0.01}
            min="0"
            value={formData.valor}
            onChange={(e) => handleChange('valor', e.target.value)}
            required
            placeholder="Digite o valor"
          />
        </div>
        <div>
          <Label htmlFor="dias">Duração (dias) *</Label>
          <Input
            id="dias"
            type="number"
            min="1"
            value={formData.dias}
            onChange={(e) => handleChange('dias', e.target.value)}
            required
            placeholder="Digite a duração em dias"
          />
        </div>
        <div>
          <Label htmlFor="status">Status *</Label>
          <Select
            options={[
              { value: 'ATIVO', label: 'Ativo' },
              { value: 'INATIVO', label: 'Inativo' }
            ]}
            value={{ value: formData.status, label: formData.status === 'ATIVO' ? 'Ativo' : 'Inativo' }}
            onChange={(option) => handleChange('status', (option as any)?.value || 'ATIVO')}
            placeholder="Selecione o status"
          />
        </div>
      </div>

      {formData.valor && formData.dias && (
        <div className="bg-gray-50 dark:bg-gray-800 p-4 rounded-lg">
          <h4 className="text-sm font-medium text-gray-900 dark:text-white mb-2">
            Resumo do Plano
          </h4>
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <span className="text-gray-500 dark:text-gray-400">Valor total:</span>
              <span className="ml-2 font-medium text-gray-900 dark:text-white">
                R$ {parseFloat(formData.valor || '0').toLocaleString('pt-BR', { minimumFractionDigits: 2 })}
              </span>
            </div>
            <div>
              <span className="text-gray-500 dark:text-gray-400">Valor por dia:</span>
              <span className="ml-2 font-medium text-gray-900 dark:text-white">
                R$ {(parseFloat(formData.valor || '0') / parseInt(formData.dias || '1')).toLocaleString('pt-BR', { minimumFractionDigits: 2 })}
              </span>
            </div>
          </div>
        </div>
      )}

      <div className="flex justify-end gap-3 pt-4">
        <Button
          type="button"
          variant="outline"
          onClick={onCancel}
          disabled={loading}
        >
          Cancelar
        </Button>
        <Button
          type="submit"
          variant="primary"
          disabled={loading}
        >
          {loading ? 'Salvando...' : plano ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default PlanoForm;
