import React from 'react';
import { formatCurrency } from '@/utils/format';
import { Plano } from '@/services/api/plano.service';
import Badge from '../ui/badge/Badge';
import ButtonCard from '../ui/button/ButtonCard';

type PlanoCardProps = {
  plano: Plano;
  onEdit: () => void;
  onDelete: () => void;
  onToggleStatus: () => void;
};

const PlanoCard: React.FC<PlanoCardProps> = ({
  plano,
  onEdit,
  onDelete,
  onToggleStatus
}) => {
  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div>
        <div className="flex justify-between items-start mb-3">
          <div className="flex-1">
            <div className="flex items-center gap-2 flex-wrap overflow-x-hidden">
              <h3 className="font-medium text-gray-900 dark:text-white">
                {plano.nome}
              </h3>
              <Badge
                color={plano.status === 'ATIVO' ? 'success' : 'error'}
                className="capitalize"
              >
                {plano.status === 'ATIVO' ? 'Ativo' : 'Inativo'}
              </Badge>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
              Valor: {formatCurrency(plano.valor)}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Duração: {plano.dias} dias
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Valor por dia: {formatCurrency(plano.valor / plano.dias)}
            </p>
          </div>
          <div className="flex gap-2">
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar"
              icon="edit"
            />
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onToggleStatus}
              title={plano.status === 'ATIVO' ? 'Desativar' : 'Ativar'}
              icon={plano.status === 'ATIVO' ? 'stop' : 'play'}
            />
            <ButtonCard
              variant="danger"
              size="xs"
              onClick={onDelete}
              title="Excluir"
              icon="delete"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default PlanoCard;
