'use client';

import React, { useCallback, useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { services } from '@/services/api';
import { Plano } from '@/services/api/plano.service';
import { formatCurrency } from '@/utils/format';
import { 
  PlusIcon,
  MagnifyingGlassIcon
} from '@heroicons/react/24/outline';
import Input from '@/components/form/input/InputField';
import Button from '@/components/ui/button/Button';
import Pagination from '@/components/common/Pagination';
import { Modal } from '@/components/ui/modal';
import PlanoCard from '@/components/admin/PlanoCard';
import PlanoForm from '@/components/admin/PlanoForm';
import Label from '@/components/form/Label';
import Select from '@/components/ui/Select';
import toast from 'react-hot-toast';

export default function PlanosManagement() {
  const router = useRouter();
  const [planos, setPlanos] = useState<Plano[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(9);
  const [statusFilter, setStatusFilter] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedPlano, setSelectedPlano] = useState<Plano | null>(null);

  const loadPlanos = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.plano.list({
        page: currentPage,
        perPage: itemsPerPage,
        search: searchTerm || undefined,
        ativo: statusFilter === 'ativo' ? true : statusFilter === 'inativo' ? false : undefined
      });
      
      setPlanos(response.data || []);
      setTotalPages(response.pagination?.totalPages || 1);
    } catch (error) {
      console.error('Erro ao carregar planos:', error);
      toast.error('Erro ao carregar planos');
    } finally {
      setLoading(false);
    }
  }, [currentPage, itemsPerPage, searchTerm, statusFilter]);

  useEffect(() => {
    loadPlanos();
  }, [loadPlanos]);

  const handleEdit = (plano: Plano) => {
    setSelectedPlano(plano);
    setIsModalOpen(true);
  };

  const handleSave = async (data: any) => {
    try {
      if (selectedPlano) {
        await services.plano.update(selectedPlano.id, data);
        toast.success('Plano atualizado com sucesso');
      } else {
        await services.plano.create(data);
        toast.success('Plano criado com sucesso');
      }
      setIsModalOpen(false);
      setSelectedPlano(null);
      loadPlanos();
    } catch (error) {
      console.error('Erro ao salvar plano:', error);
      toast.error('Erro ao salvar plano');
    }
  };

  const handleDelete = async (id: number) => {
    if (!confirm('Tem certeza que deseja excluir este plano?')) {
      return;
    }

    try {
      await services.plano.delete(id);
      toast.success('Plano excluído com sucesso');
      loadPlanos();
    } catch (error) {
      console.error('Erro ao excluir plano:', error);
      toast.error('Erro ao excluir plano');
    }
  };

  const handleToggleStatus = async (plano: Plano) => {
    const newStatus = plano.status === 'ATIVO' ? 'INATIVO' : 'ATIVO';
    const action = plano.status === 'ATIVO' ? 'desativar' : 'ativar';
    
    if (window.confirm(`Tem certeza que deseja ${action} o plano ${plano.nome}?`)) {
      try {
        await services.plano.update(plano.id, { status: newStatus });
        toast.success(`Plano ${plano.nome} ${action === 'desativar' ? 'desativado' : 'ativado'} com sucesso!`);
        loadPlanos();
      } catch (error) {
        toast.error(`Erro ao ${action} plano.`);
        console.error(error);
      }
    }
  };

  const statusOptions = [
    { value: '', label: 'Todos' },
    { value: 'ativo', label: 'Ativos' },
    { value: 'inativo', label: 'Inativos' }
  ];

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
              Gerenciar Planos
            </h1>
            <p className="text-sm text-gray-500">
              Gerencie todos os planos do sistema
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button
              variant="primary"
              onClick={() => {
                setSelectedPlano(null);
                setIsModalOpen(true);
              }}
            >
              Novo Plano
            </Button>
          </div>
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar planos..."
              value={searchTerm}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearchTerm(e.target.value)}
              className="w-full"
            />
          </div>
          <div className="flex flex-wrap gap-3">
            <Select
              placeholder="Status"
              options={statusOptions}
              value={statusOptions.find(opt => opt.value === statusFilter)}
              onChange={(option) => setStatusFilter((option as any)?.value || '')}
              isClearable
              className="whitespace-nowrap min-w-40"
            />
          </div>
        </div>
      </div>

      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className='flex items-center gap-0.5'>
          <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
          <Input
            className='max-w-24'
            defaultValue={itemsPerPage}
            max={100}
            min={1}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const newValue = Number(e.target.value) || 9;
              setItemsPerPage(newValue);
              setCurrentPage(1);
            }}
          />
        </div>
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={setCurrentPage}
        />
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {planos.length > 0 ? (
            planos.map((plano) => (
              <PlanoCard
                key={plano.id}
                plano={plano}
                onEdit={() => handleEdit(plano)}
                onDelete={() => handleDelete(plano.id)}
                onToggleStatus={() => handleToggleStatus(plano)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhum plano encontrado
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={currentPage}
              totalPages={totalPages}
              onPageChange={setCurrentPage}
            />
          </div>
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedPlano(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedPlano ? 'Editar Plano' : 'Novo Plano'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedPlano 
                ? 'Atualize as informações do plano.' 
                : 'Preencha os dados para criar um novo plano.'
              }
            </p>
          </div>
          <PlanoForm
            plano={selectedPlano}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedPlano(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}